<template>
  <div class="register">
    <CommonHeader title="注册" />
    <div class="logoWrapper">
      <img class="logo" src="../assets/6.gif" />
    </div>
    <van-cell-group class="form">
      <van-field
        v-model="username"
        required
        clearable
        size="large"
        label="账号"
        placeholder="请输入6-18位的账号"
        error-message=""
      />
      <van-field
        v-model="password"
        type="password"
        label="密码"
        size="large"
        placeholder="请输入6-18位的密码"
        required
        error-message=""
      />
      <van-field
        v-model="confirm"
        type="password"
        label="确认密码"
        size="large"
        placeholder="请输入6-18位的密码"
        required
        error-message=""
      />
      <div class="code_container">
        <div class="code_title">验证码</div>
        <div class="code_input">
          <input
            type="text"
            v-model="code"
            placeholder="请输入验证码"
            id="code"
          />
        </div>
        <div class="code_img">
          <van-image
            class="code"
            :src="imgUrl"
            @click="changeVerifyCode"
            alt="验证码"
          >
            <template v-slot:loading>
              <van-loading type="spinner" size="20" />
            </template>
          </van-image>
        </div>
      </div>
    </van-cell-group>

    <div class="link">
      <RouterLink to="/login">快速登录</RouterLink>
    </div>
    <div class="submit">
      <van-button
        type="primary"
        @click="register"
        class="btn"
        size="normal"
        :block="true"
        >注册</van-button
      >
    </div>
  </div>
</template>
<script>
import axios from "@/axios";
import CommonHeader from "@/components/common/CommonHeader.vue";
const qs = require("qs");
export default {
  name: "Register",
  data() {
    return {
      username: "",
      password: "",
      confirm: "",
      code: "",
      imgUrl: "https://timepro.xjinfeng.cn/code"
    };
  },

  components: {
    CommonHeader
  },
  methods: {
    changeVerifyCode() {
      this.imgUrl = "https://timepro.xjinfeng.cn/code?id=" + Math.random();
    },
    register() {
      if (/\w{6,18}$/.test(this.username)) {
        if (/\w{6,18}$/.test(this.password)) {
          if (this.password != this.confirm) {
            this.$toast("密码和确认密码不一致");
            return;
          }
          axios({
            method: "post",
            url: "/register",
            data: qs.stringify({
              userId: this.username,
              password: this.password,
              code: this.code
            }),
            headers: {
              "Content-Type": "application/x-www-form-urlencoded"
            }
          }).then(res => {
            if (res.data.code === 200) {
              this.$toast("注册成功");
              // window.location.href = "/login"
              this.$router.push({ path: "/login" });
            }
          });
        } else {
          this.$toast("密码只能由6-18位的字母、数字或下划线组成");
        }
      } else {
        this.$toast("账号只能由6-18位的字母、数字或下划线组成");
      }
    }
  }
};
</script>
